
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.02rem;
  font-size: 14px;
  background: #fff;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item .icon-img {
  display: none;
}
.tab .tab-item .icon-img2 {
  display: block;
  width: 0.36rem;
  height: 0.36rem;
  margin: 0.16rem auto 0.13rem;
}
.tab .tab-item .tab-link {
  padding-bottom: 0.19rem;
  color: #ccc;
  display: block;
  font-size: 0.2rem;
  line-height: 0.16rem;
}
.tab .tab-item.router-link-active .icon-img {
  display: block;
  width: 0.36rem;
  height: 0.36rem;
  margin: 0.16rem auto 0.13rem;
}
.tab .tab-item.router-link-active .icon-img2 {
  display: none;
}
.tab .tab-item.router-link-active .tab-link {
  color: #ffcd32;
  font-size: 0.2rem;
  line-height: 0.16rem;
  padding-bottom: 0.19rem;
  border-bottom: 2px solid #ffcd32;
}


